<template>
<div class="">
	<fixedheader :title="title"></fixedheader>
	
	<div class="orders" style="margin-top: .2rem;">
		<div class="order" @click="duihuandetail(item)" v-for="item in items">
			<div class="bg-pad1 flex-item">
				<div>订单号 : {{item.order_sn}}</div>
				<div class=" active-color" v-if="item.type == 0">未发货</div>	
				<div class=" active-color" v-if="item.type == 1">已发货</div>
				<div class=" active-color" v-if="item.type == 2">已收货</div>
			</div>
			<div class="bg-pad order-goods flex-wrap">
				<div><img :src="item.good_pic"/></div>
				
				<div>
					<div>{{item.good_name}}</div>
					<div  class="flex-item arrr">
						<div>红色</div>
						<div>X{{item.good_num}}</div>
					</div>
					<div class="price">￥255.00</div>
				</div>
			</div>			
			<div class="total">
				共1件商品,合计:1000积分
			</div>
			
		</div>
				
	</div>
</div>
</template>

<script>
import fixedheader from "../common/FixedHeader"
export default{
	data(){
		return{
			title:'兑换记录',
			items:[]
		}
	},
	methods:{
		duihuandetail(item){
			this.$router.push('/duihuandetail/'+item.id)
		},
		getJiFenOrder(page){
     	this.post('home/jifen/getJiFenOrder',{page:page,number:20}).then(function(res){  	 		
//			console.log(res)
			if(res.data.code===10000){
    			this.items=res.data.info.info;    		
    			
    			this.items.forEach(function(item){
    				item.good_pic=this.imageURL(item.good_pic)
    				
    			}.bind(this))
    			console.log(this.items)
    		}
    	}.bind(this))
     },
	},
	components:{fixedheader},
	mounted(){
      this.getJiFenOrder(1)
    }
}
</script>

<style scoped>
/**/
.arrr{color: #999;font-size: .25rem;}
.flex-wrap{display: flex;flex-direction: row;align-items: center;}
.flex-wrap1{display: flex;flex-direction: row;align-items: center;padding: .2rem 0;}
.flex-item{display: flex;flex-direction: row;justify-content: space-between;align-items: center;}
.order-state{margin: .2rem 0;}
.bg-pad{background: #F9F9F9;padding: .2rem;}
.bg-pad1{background: #fff;padding: .2rem;}
/*.order-goods{margin-top: .2rem;}*/
.order-goods img{width: 1.8rem;height: 1.8rem;margin-right: .2rem;}
.msg{margin-bottom: .2rem;}

.fixed-bottom{position: fixed;bottom:0;max-width: 750px;margin: auto;color: #fff;
 width: 100%;height: 1rem;line-height: 1rem; background:#fff ;text-align: center;display: flex;align-items: center;}
 
 .fixed-bottom div{
 	flex: 1;
 }
 .fixed-bottom div:nth-of-type(1){color: #444;}
 .fixed-bottom div:nth-of-type(2){background: #FF5773;}
 .active-color{color: #ff5773;}
 .paid{border-top: 1px #e6e6e6 solid;}
/**/
.order-inner{background: #f5f5f5;}
.flex-wrap1{display: flex;}
.flex-wrap1 div{flex:0 0 16.6%;text-align: center;padding-bottom: 5px;}
.order-tab-active{position: relative;}
.order-tab-active:after{
	content:'';
	position:absolute;
	bottom:0;
	left:23%;
	height:0px;
	width:50%;
	border:1px solid #ff5773;	
}
.fixed-nav{
	/*height: .7rem;*/
	background:#fff;
}
.fixed-nav .flex-wrap{
	position: fixed;
	top: .8rem;
	left: 0;
	right: 0;
	width: 100%;
	margin: auto;
	max-width: 640px;
}
.order-tab-active{
	color:#ff5773;	
}
.fixed-nav .flex-wrap{
	display: flex;
	color: #ff5773;
	
}
.fixed-nav .flex{
	flex: 1;
	text-align: center;
}
.orders{
	color: #111;
	padding-bottom: .2rem;
	padding-bottom: 60px;
}
.order{
	color: #111;
	background: #fff;
	margin-bottom: .2rem;
}
.order-top{
	padding:0 .2rem;
	height: .6rem;
	line-height:.6rem;
	
}
.order-top .del{
	width: .4rem;
	display: block;
	margin-top:.13rem;
}
.order-inner{
	padding: .2rem;
	border-bottom:.01rem #f1f1f1 solid;
	border-top:.01rem #f1f1f1 solid;
	display: flex;
}
.order-inner .goods-img{
	width: 1rem;
	height: 1rem;
	
}
.goods-img-box{
	margin-right: .3rem;
}
.order-infor{
	flex: 2;
}
.order-infor .f_left{color: #5a5a5a;}
.goods-img-box{
	width:1.6rem;
	height:1.6rem;
	line-height:1.6rem;
	text-align:center;
}
.goods-img-box img{
	width:100%;
	vertical-align:middle;
}
.goods-img-box img{
	width: 1.6rem;
	height: 1.6rem;
}
.order-infor .goods-name{
	font-weight: bolder;
}
.over2{
	overflow: hidden;
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 2;
}
.order-infor .quantity{
	color: #666;
}
.order-infor .price{
	color: #F2483D;
	font-size: .3rem;
	font-weight: bolder;
	margin-top: 8px;
}
.order-infor .goodsinfo{
	height: .5rem;
	line-height: .5rem;
}
.order-num{
	height: .8rem;
	line-height:.8rem;
	padding: 0 .2rem;
	border-bottom:.01rem #f1f1f1 solid;
	color: #5A5A5A;
}
.total{
	height: .8rem;
	line-height:.8rem;
	padding: 0 .2rem;
	border-bottom:.01rem #f1f1f1 solid;
	color: #5A5A5A;
	text-align: right;
}
.order-bottom{
	padding:.3rem .2rem;
	text-align: right;
}
.order-bottom span{float: left;}
.order-bottom a{
	text-align: right;
	padding: .1rem .3rem;
	
	margin-left: .2rem;
	border-radius: 4px;
}
.grey-border{
	border: .01rem #333 solid;
	color: #333;
}
.active-border{
	border: .01rem #ff5773 solid;
	color: #ff5773;
}
.flex-item{display: flex;flex-direction: row;justify-content: space-between;align-items: center;}
</style>


